import axios from 'axios';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { nextTick } from 'vue';
import qs from 'qs';
const NO_USE_LOADING = [
    '/hbact/ossCommon/uploadOne',
    '/hbSeller/seller/rebate/detail',
    '/syx/awd/list'
];
axios.defaults.timeout = 60000;
axios.interceptors.request.use((config) => {
    if (!NO_USE_LOADING.includes(config.url)) {
        Toast.loading({
            message: '加载中...',
            forbidClick: true,
            duration: 0
        });
    }
    return config;
});
axios.interceptors.response.use((response) => {
    nextTick(() => {
        Toast.clear();
    });
    return response;
}, (e) => {
    nextTick(() => {
        if (process.env.NODE_ENV === 'production') {
            window.location.href = '/yx/views/general/overtime-error.html';
        }
        Toast.clear();
    });
    return Promise.reject(e);
});
const send = async (config) => {
    try {
        const { data: res } = await axios(config);
        return Promise.resolve(res);
    }
    catch (err) {
        return Promise.reject(err);
    }
};
export const http = {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    get: async (url, params) => {
        const config = {
            method: 'GET',
            url,
            params
        };
        return send(config);
    },
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    post: async (url, data, jsonFlag) => {
        const config = {
            method: 'POST',
            url,
            data
        };
        if (jsonFlag) {
            config.headers = {
                'Content-type': 'application/json;charset=UTF-8'
            };
            config.data = JSON.stringify(data);
        }
        else {
            config.data = qs.stringify(data);
        }
        return send(config);
    }
};
export default axios;
