import Button from "vant/es/button";
import "vant/es/button/style/less";
import List from "vant/es/list";
import "vant/es/list/style/less";
import Cell from "vant/es/cell";
import "vant/es/cell/style/less";
import Tabbar from "vant/es/tabbar";
import "vant/es/tabbar/style/less";
import Dialog from "vant/es/dialog";
import "vant/es/dialog/style/less";
import TabbarItem from "vant/es/tabbar-item";
import "vant/es/tabbar-item/style/less";
import Icon from "vant/es/icon";
import "vant/es/icon/style/less";
import NavBar from "vant/es/nav-bar";
import "vant/es/nav-bar/style/less";
import Field from "vant/es/field";
import "vant/es/field/style/less";
import ActionSheet from "vant/es/action-sheet";
import "vant/es/action-sheet/style/less";
import Uploader from "vant/es/uploader";
import "vant/es/uploader/style/less";
import Form from "vant/es/form";
import "vant/es/form/style/less";
import CellGroup from "vant/es/cell-group";
import "vant/es/cell-group/style/less";
import Picker from "vant/es/picker";
import "vant/es/picker/style/less";
import Popup from "vant/es/popup";
import "vant/es/popup/style/less";
import Radio from "vant/es/radio";
import "vant/es/radio/style/less";
import RadioGroup from "vant/es/radio-group";
import "vant/es/radio-group/style/less";
import DatetimePicker from "vant/es/datetime-picker";
import "vant/es/datetime-picker/style/less";
import Cascader from "vant/es/cascader";
import "vant/es/cascader/style/less";
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import Tab from "vant/es/tab";
import "vant/es/tab/style/less";
import Tabs from "vant/es/tabs";
import "vant/es/tabs/style/less";
import Checkbox from "vant/es/checkbox";
import "vant/es/checkbox/style/less";
import CheckboxGroup from "vant/es/checkbox-group";
import "vant/es/checkbox-group/style/less";
import Overlay from "vant/es/overlay";
import "vant/es/overlay/style/less";
const plugins = [
    Button,
    List,
    Cell,
    Dialog,
    Tabbar,
    TabbarItem,
    Icon,
    NavBar,
    Field,
    ActionSheet,
    Uploader,
    Form,
    CellGroup,
    Picker,
    Popup,
    Radio,
    RadioGroup,
    DatetimePicker,
    Cascader,
    Toast,
    Tab,
    Tabs,
    Checkbox,
    CheckboxGroup,
    Overlay
];
export const vantPlugins = {
    install: function (vm) {
        plugins.forEach((item) => {
            vm.component(item.name, item);
        });
    }
};
