import { createStore } from 'vuex';
const store = createStore({
    state: {
        loading: false,
        error: {
            status: false
        },
        shopCode: sessionStorage.getItem('shopCode') || '',
        token: sessionStorage.getItem('token') || '',
        lat: null,
        lng: null,
        wxUrl: null,
        bindChannel: sessionStorage.getItem('bindChannel') || '',
        bindShopFlag: true,
        needComplete: sessionStorage.getItem('needComplete')
            ? sessionStorage.getItem('needComplete') === '1'
                ? true
                : false
            : null,
        hasMsg: null
    },
    getters: {},
    mutations: {
        setLat(state, lat) {
            state.lat = lat;
        },
        setLng(state, lng) {
            state.lng = lng;
        },
        setWxUrl(state, url) {
            state.wxUrl = url;
        },
        setToken(state, token) {
            state.token = token;
        },
        setShopCode(state, shopCode) {
            state.shopCode = shopCode;
        },
        setBindChannel(state, bindChannel) {
            state.bindChannel = bindChannel;
        },
        setLoading(state, status) {
            state.loading = status;
        },
        setNeedComplete(state, status) {
            state.needComplete = status;
        },
        setHasMsg(state, status) {
            state.hasMsg = status;
        },
        setError(state, e) {
            state.error = e;
        },
        setBindShopFlag(state, bindShopFlag) {
            state.bindShopFlag = bindShopFlag;
        }
    },
    actions: {}
});
export default store;
